import { createSelector } from "@reduxjs/toolkit";
import { memo, useEffect, useState } from "react";
import isEqual from "react-fast-compare";
import { FaMinus } from "react-icons/fa";
import { useSelector } from "react-redux";
import { selectCurSetupI, selectSetups } from "../../../app/calculatorSlice";
import { getThisChar } from "../../../app/calculatorSlice/helpers";
import { getRxnBnesFromEM } from "../../../calculators/helpers";
import { bareLv } from "../../../helpers";
import { useCloseWithEsc } from "../../../helpers/hooks";
import { lib } from "../../../library";
import { Green, Text } from "../../../styledCpns/DataDisplay";
import { IconBtn } from "../../../styledCpns/Inputs";
import { AbsoluteBox, Box } from "../../../styledCpns/Layout";
import { CloseBtn, ExpandableList } from "../../CommonCpns";
import { ModalContent } from "../../CommonCpns/Modal";
import { Attributes, Bonuses } from "./AllStats";
import Damage from "./Damage";
import Debuffs from "./Debuffs";
import { calculateAndTrack, getTotal } from "./helpers";
import Reactions from "./Reactions";
import { Lesser, LesserSpan, TrackerModal } from "./styles";

const selCurSetupName = createSelector(
  selectSetups,
  selectCurSetupI,
  (setups, curI) => setups[curI].name
);

export default function TrackerLayout({ trackerState, setTrackerState }) {
  const name = useSelector(selCurSetupName);
  const calcInfo = useSelector((state) => state.calculator);
  const char = getThisChar(calcInfo.char, calcInfo.curSetupI);
  const [result, setResult] = useState();
  const close = () => setTrackerState(0);
  useCloseWithEsc(close);

  useEffect(() => {
    if (trackerState === 1) {
      setResult(calculateAndTrack(char, calcInfo));
    }
  }, [trackerState]);

  return (
    <TrackerModal state={trackerState}>
      <Box
        className="full-w full-h"
        bgColor="rgba(0, 0, 0, .6)"
        onClick={() => setTrackerState(0)}
      />
      <ModalContent className="p-6 flex-col">
        <AbsoluteBox className="flex" top="0.75rem" right="0.75rem">
          <IconBtn
            className="mr-4 glow-on-hover"
            size="1.75rem"
            variant="default"
            onClick={() => setTrackerState(2)}
          >
            <FaMinus size="1.25rem" />
          </IconBtn>
          <CloseBtn className="glow-on-hover" onClick={close} />
        </AbsoluteBox>
        {window.innerWidth >= 769 ? (
          <div className="flex justify-center">
            <Text
              variant="h2"
              color="orange"
              onClick={() => console.log(result)}
            >
              Tracking Results
            </Text>
            <Lesser className="ml-2">({name})</Lesser>
          </div>
        ) : (
          <Text variant="h2" color="orange">
            {name}
          </Text>
        )}
        <MemoTracker
          result={result}
          charLv={bareLv(char.level)}
          target={calcInfo.target}
        />
      </ModalContent>
    </TrackerModal>
  );
}

const MemoTracker = memo(Tracker, isEqual);

function Tracker({ result, charLv, target }) {
  if (!result) return null;
  const [tracker, infusion, ATTRs, hitBnes, rxnBnes, dmg] = result;
  const rxnBnesFrEM = getRxnBnesFromEM(ATTRs["Elemental Mastery"]);
  const totalDefRd = getTotal(tracker.Def_rd);
  const forDefMult = {
    char: (
      <>
        <LesserSpan>char. Lv.</LesserSpan> <Green>{charLv}</Green> + 100
      </>
    ),
    target: (
      <>
        (<LesserSpan>target Lv.</LesserSpan> <Green>{target.Level}</Green> +
        100)
      </>
    ),
    rd: totalDefRd ? (
      <>
        (1 - <LesserSpan>DEF reduction</LesserSpan> <Green>{totalDefRd}</Green>{" "}
        / 100) *
      </>
    ) : null
  };
  return (
    <div className="mt-4 custom-sb scroll-smooth">
      <ExpandableList
        headingList={[
          "Attributes",
          "Bonuses",
          "Debuffs on Target",
          "Normal Attacks",
          "Elemental Skill",
          "Elemental Burst",
          "Reactions"
        ]}
        contentList={[
          <Attributes ATTRs={ATTRs} tracker={tracker} />,
          <Bonuses
            hitBnes={hitBnes}
            rxnBnesFrEM={rxnBnesFrEM}
            rxnBnes={rxnBnes}
            tracker={tracker}
          />,
          <Debuffs target={target} tracker={tracker} />,
          <Damage
            infusion={infusion}
            forDefMult={forDefMult}
            dmg={dmg[lib.NA]}
            defIg={getTotal(tracker.NA_ig)}
            tracker={tracker[lib.NA]}
          />,
          <Damage
            forDefMult={forDefMult}
            dmg={dmg[lib.ES]}
            defIg={getTotal(tracker.ES_ig)}
            tracker={tracker[lib.ES]}
          />,
          <Damage
            forDefMult={forDefMult}
            dmg={dmg[lib.EB]}
            defIg={getTotal(tracker.EB_ig)}
            tracker={tracker[lib.EB]}
          />,
          <Reactions
            dmg={dmg["Reactions DMG"]}
            charLv={charLv}
            tracker={tracker.Reactions}
          />
        ]}
      />
    </div>
  );
}
