import { useEffect, useState } from "react";
import { FaSave, FaSyncAlt, FaTrashAlt } from "react-icons/fa";
import { useDispatch, useSelector } from "react-redux";
import {
  CHANGE_ART_MS_TYPE,
  CHANGE_ART_SS_TYPE,
  CHANGE_ART_SS_VAL,
  ENHANCE_ART,
  SET_ART_PIECE
} from "../../../app/calculatorSlice";
import {
  ADD_ART,
  OVERWRITE_ART,
  selectMyArts
} from "../../../app/databaseSlice";
import { pctOf } from "../../../configs";
import { artMS } from "../../../data/artifacts/config";
import { btnState, findById, isOne } from "../../../helpers";
import { Text } from "../../../styledCpns/DataDisplay";
import { IconBtn, Select } from "../../../styledCpns/Inputs";
import { Box } from "../../../styledCpns/Layout";
import { ConfirmDialog } from "../../CommonCpns";
import { Collapse } from "../../CommonCpns/HiddenSpace";
import Modal from "../../CommonCpns/Modal";
import ArtSubstats from "../../Readers/ArtifactCard/ArtSubstats";

export default function ArtPieceInfo({ pieces, tabI, setTabI, setPickerI }) {
  return (
    <Collapse in={tabI !== -1}>
      {pieces[tabI] && (
        <FilledCard
          artP={pieces[tabI]}
          pieceIndex={tabI}
          setTabI={setTabI}
          setPickerI={setPickerI}
        />
      )}
    </Collapse>
  );
}

function FilledCard({ artP, pieceIndex, setTabI, setPickerI }) {
  const { rarity = 5, mainSType } = artP;
  const availableMSTypes = artMS[artP.type];
  const isFoP = ["flower", "plume"].includes(artP.type);
  const msValues = availableMSTypes[mainSType][rarity];
  const [saving, setSaving] = useState(false);
  const dispatch = useDispatch();
  const maxLv = rarity === 5 ? 20 : 16;

  return (
    <div className="pt-6" onDoubleClick={() => console.log(artP)}>
      <div className="pl-6 flex align-start">
        <Box className="mb-2" bgColor="cardBg3" borderRadius="50%">
          <Select
            color={rarity}
            fontSize="1.125rem"
            bold
            className="appearance-none px-2 pt-2 pb-1 pointer"
            value={"+" + artP.level}
            onChange={(e) =>
              dispatch(
                ENHANCE_ART({
                  pieceIndex,
                  level: +e.target.value.slice(1)
                })
              )
            }
          >
            {[...Array(rarity === 5 ? 21 : 17).keys()].map((_, lv) => (
              <option key={lv}>+{lv}</option>
            ))}
          </Select>
        </Box>
        <div className="ml-4">
          {isFoP ? (
            <Text className="pl-8 pt-1" variant="h6">
              {mainSType}
            </Text>
          ) : (
            <div className="py-1">
              <Select
                fontSize="1.125rem"
                color="white"
                className="pl-8 appearance-none bg-for-icon white-arrow-bg"
                value={mainSType}
                onChange={(e) =>
                  dispatch(
                    CHANGE_ART_MS_TYPE({ pieceIndex, type: e.target.value })
                  )
                }
              >
                {Object.keys(availableMSTypes).map((type) => (
                  <option key={type}>{type}</option>
                ))}
              </Select>
            </div>
          )}
          <Text className="pl-8" variant="h3" color={rarity}>
            {msValues[artP.level] + pctOf(mainSType)}
          </Text>
        </div>
      </div>
      <div className="px-2 pb-1">
        <ArtSubstats
          mutable={true}
          substats={artP.subS}
          changeSSType={(type, ssIndex) =>
            dispatch(CHANGE_ART_SS_TYPE({ pieceIndex, ssIndex, type }))
          }
          changeSSVal={(value, ssIndex) =>
            dispatch(CHANGE_ART_SS_VAL({ pieceIndex, ssIndex, value }))
          }
          msType={mainSType}
          rarity={rarity}
        />
      </div>
      <div className="mt-4 flex justify-evenly align-center">
        <IconBtn
          className="glow-on-hover"
          variant="negative"
          onClick={() => {
            dispatch(SET_ART_PIECE({ pieceIndex, newPiece: null }));
            setTabI(-1);
          }}
        >
          <FaTrashAlt />
        </IconBtn>
        <IconBtn
          className="glow-on-hover"
          variant="neutral"
          onClick={() => setSaving(true)}
        >
          <FaSave />
        </IconBtn>
        <IconBtn
          className={btnState(artP.level !== maxLv)}
          variant="neutral"
          onClick={() => dispatch(ENHANCE_ART({ pieceIndex, level: maxLv }))}
        >
          {maxLv}
        </IconBtn>
        <IconBtn
          className="glow-on-hover"
          variant="positive"
          fontSize="1.25rem"
          onClick={() => setPickerI(pieceIndex)}
        >
          <FaSyncAlt />
        </IconBtn>
      </div>
      {saving && <SavingDialog artP={artP} close={() => setSaving(false)} />}
    </div>
  );
}

function SavingDialog({ artP, close }) {
  const [type, setType] = useState(0);
  const existed = findById(useSelector(selectMyArts), artP.ID);
  const dispatch = useDispatch();

  useEffect(() => {
    if (existed) {
      setType(2);
    } else {
      dispatch(ADD_ART({ user: null, ...artP }));
      setType(1);
    }
  }, []);

  if (type === 0) return <Modal />;

  const successful = type === 1;
  let noChange;
  if (existed) {
    const { user, ...info } = existed;
    noChange = isOne(artP, info);
  }
  const xtra = existed?.user ? (
    <>
      , currently used by <b>{existed.user}</b>
    </>
  ) : null;
  const message = successful ? (
    <>Successfully saved to My Artifacts.</>
  ) : (
    <>
      This one is already in My Artifacts{xtra}.{" "}
      {noChange
        ? "Nothing has changed."
        : "Do you want to overwrite its Stats?"}
    </>
  );

  return (
    <ConfirmDialog
      message={message}
      left={successful && { text: "Close" }}
      mid={
        !successful && {
          text: "Duplicate",
          handleClick: () =>
            dispatch(ADD_ART({ user: null, ...artP, ID: Date.now() }))
        }
      }
      confirm={() => {
        if (successful || noChange) {
          close();
        } else {
          dispatch(OVERWRITE_ART(artP));
        }
      }}
      close={close}
    />
  );
}
