import styled from "styled-components";
import { swirlables } from "../../../configs";
import { Text } from "../../../styledCpns/DataDisplay";
import { Checkbox, Select, TextInput } from "../../../styledCpns/Inputs";
import { Space } from "../../../styledCpns/Layout";

export function Section(props) {
  return (
    <div className="pt-2">
      <div className="pt-1 mb-1 flex">
        <label className="flex align-center">
          {!props.immutable && (
            <Checkbox
              className="ml-1 mr-2"
              scale={1.6}
              checked={props.checked}
              onChange={props.handleCheck}
            />
          )}
          <Text className="pl-1" bold color="lightGold">
            {props.heading}
          </Text>
        </label>
      </div>
      <Text>{props.desc}</Text>
      <Space className="pt-2 pr-2" gap="1.25rem">
        {props.setters}
      </Space>
    </div>
  );
}

export const StyledSetter = styled.div`
  display: flex;
  justify-content: flex-end;
  align-items: center;
  .label {
    margin-right: 1rem;
    font-size: 1rem;
    line-height: 1.5;
    text-align: right;
  }
`;

export const StyledSelect = styled((props) => (
  <Select bgColor="white" borderRadius="0.25rem" bold {...props} />
))`
  padding: 0.25rem 0.5rem;
`;

export const NoContent = ({ type }) => (
  <Text className="pt-6 pb-4" align="center">
    No {type} found
  </Text>
);

export function CharModSetter({
  labels,
  mod,
  inputs,
  handleText,
  handleCheck,
  handleSelect
}) {
  return labels.map((label, i) => {
    const type = mod.inputTypes[i];
    let inputCpn;
    switch (type) {
      case "text":
        inputCpn = (
          <TextInput
            className="px-2 py-2"
            width="4.25rem"
            borderRadius="0.25rem"
            align="right"
            value={inputs[i]}
            onChange={(e) => handleText(e.target.value, i)}
          />
        );
        break;
      case "check":
        inputCpn = (
          <Checkbox
            className="mr-1"
            scale={1.6}
            checked={inputs[i]}
            onChange={() => handleCheck(i)}
          />
        );
        break;
      default:
        let options;
        switch (type) {
          case "select":
            // options = [...Array(mod.maxs[i]).keys()];
            options = Array.from({ length: mod.maxs[i] }, (_, i) => i + 1);
            // if (startsAt0 && startsAt0[i]) options.unshift(0);
            break;
          case "dendroable":
            options = ["Pyro", "Hydro", "Electro"];
            break;
          case "scara":
            options = ["Pyro", "Hydro", "Cryo"];
            break;
          default:
            // swirl & absorption
            options = swirlables;
        }
        inputCpn = (
          <StyledSelect
            value={inputs[i]}
            onChange={(e) => handleSelect(e.target.value, i)}
          >
            {options.map((opt, j) => (
              <option key={j}>{opt}</option>
            ))}
          </StyledSelect>
        );
    }
    return (
      <StyledSetter key={i}>
        <p className="label">{label}</p>
        {inputCpn}
      </StyledSetter>
    );
  });
}
