import styled from "styled-components";
import { Table } from "../../../styledCpns/DataDisplay";
import { colors } from "../../../theme";

export const StyledTable = styled(Table)`
  margin-bottom: 0.5rem;
  .sticky-col {
    position: sticky;
    z-index: 2;
    left: 0;
    min-width: 8.5rem;
    background-color: inherit;
  }
  .limited-width {
    width: 8.5rem;
  }
  .long-arrow {
    position: absolute;
    top: 50%;
    right: 0.375rem;
    margin-top: -0.425rem;
    font-size: inherit;
  }
  .higher {
    color: ${colors.green};
  }
  .lower {
    color: #ff5d61;
  }
  .tooltip_wrapper {
    padding-right: 1.125rem !important;
    min-width: 5rem;
  }
  .tooltip_wrapper:hover .tooltip {
    display: block;
  }
  .tooltip {
    position: absolute;
    bottom: calc(50% + 0.625rem);
    right: 1rem;
    z-index: 1;
    display: none;
    padding: 0.25rem 0.5rem 0.125rem;
    border-radius: 0.25rem;
    background: black;
  }
`;
