import { baseRxnDmg, tfmRxns } from "../../../configs";
import { round2, round3 } from "../../../helpers";
import { Green, Orange, Text } from "../../../styledCpns/DataDisplay";
import { Lesser } from "./styles";

export default function Reactions({ dmg, charLv, tracker }) {
  return (
    <div>
      {Object.entries(dmg).map(([rxn, dmgPiece]) => {
        const [mult, ext, res] = tracker[rxn];
        const cRate = tracker[rxn][9] || 0;
        const cDmg = tracker[rxn][10] || 0;
        const { dmgType } = tfmRxns[rxn] || {};

        const Nc = Math.round(dmgPiece["Non-crit"]);
        const base = Math.round(baseRxnDmg[charLv] * mult);
        const Crit = Nc * (1 + cDmg);
        const Average = Nc * (1 + cRate * cDmg);

        return (
          <div key={rxn} className="pt-1">
            <Text bold>{rxn}</Text>
            <Lesser className="pl-2">
              • Non-crit <Orange b>{Math.round(Nc)}</Orange> = Base DMG{" "}
              <Green>{base}</Green> <Green>*</Green> Bonus Mult.{" "}
              <Green>{round3(ext)}</Green> <Green>*</Green>{" "}
              {dmgType === "Various" ? (
                "RES Mult. (depends on Element Swirled)"
              ) : (
                <>
                  {dmgType} RES Mult. <Green>{round2(res)}</Green>
                </>
              )}
            </Lesser>
            {!!cDmg && (
              <Lesser className="pl-2">
                • Crit <Orange b>{Math.round(Crit)}</Orange> = {Nc}{" "}
                <Green>*</Green> (<Green>1 +</Green> Crit DMG{" "}
                <Green>{cDmg}</Green>)
              </Lesser>
            )}
            {Average !== Nc && (
              <Lesser className="pl-2">
                • Average <Orange b>{Math.round(Average)}</Orange> = {Nc}{" "}
                <Green>*</Green> (<Green>1 +</Green> Crit DMG{" "}
                <Green>{cDmg}</Green> <Green>*</Green> Crit Rate{" "}
                <Green>{round2(cRate)}</Green>)
              </Lesser>
            )}
          </div>
        );
      })}
    </div>
  );
}
