import cn from "classnames";
import { memo, useCallback, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  COPY_ART,
  selectCurSetupI,
  selectSetups,
  SET_ART_PIECE
} from "../../../app/calculatorSlice";
import { artifactIcons, artTypes, wikiImg } from "../../../configs";
import { findArtifact } from "../../../data/controllers";
import { indexByName } from "../../../helpers";
import { Box, GradientBox } from "../../../styledCpns/Layout";
import { ArtPicker } from "../../Picker/ItemPicker";
import { CopySection, Pedestal } from "../styles";
import ArtPieceInfo from "./ArtPieceInfo";

export default memo(SectionArts);

const scroll = () =>
  setTimeout(() => {
    const body = document.getElementById("setupManager_body");
    body.scrollTop = 9999;
  }, 200);

function SectionArts() {
  const setups = useSelector(selectSetups);
  const artChest = useSelector((state) => state.calculator.artChest);
  const [tabI, setTabI] = useState(-1);
  const [pickerI, setPickerI] = useState(-1);
  const dispatch = useDispatch();
  const { pieces } = artChest[useSelector(selectCurSetupI)];

  const handleTabClick = useCallback(
    (index) => {
      if (pieces[index]) {
        setTabI(tabI === index ? -1 : index);
        if (tabI === -1 || tabI !== index) {
          scroll();
        }
      } else {
        setPickerI(index);
      }
    },
    [pieces, tabI]
  );

  const copyOpts = [];
  if (pieces.every((artP) => artP === null)) {
    for (let i in artChest) {
      if (artChest[i].pieces.some((artP) => artP !== null)) {
        copyOpts.push(setups[i].name);
      }
    }
  }
  return (
    <Pedestal>
      {copyOpts.length ? (
        <CopySection
          options={copyOpts}
          copy={(name) => dispatch(COPY_ART(indexByName(setups, name)))}
        />
      ) : null}
      <TabBar pieces={pieces} tabI={tabI} handleClick={handleTabClick} />
      <ArtPieceInfo
        pieces={pieces}
        tabI={tabI}
        setTabI={setTabI}
        setPickerI={setPickerI}
      />
      {pickerI !== -1 && (
        <ArtPicker
          artType={artTypes[pickerI]}
          close={() => setPickerI(-1)}
          pick={(item) => {
            dispatch(
              SET_ART_PIECE({
                pieceIndex: pickerI,
                newPiece: { ID: Date.now(), ...item },
                isNew: true
              })
            );
            setTabI(pickerI);
            scroll();
          }}
        />
      )}
    </Pedestal>
  );
}

function TabBar({ pieces, tabI, handleClick }) {
  return (
    <div className="flex">
      {artTypes.map((type, index) => {
        const artP = pieces[index];
        let src;
        if (artP) {
          const { beta, icon } = findArtifact({ code: artP.code, type });
          src = beta ? icon : wikiImg(icon);
        } else {
          src = wikiImg(artifactIcons[type]);
        }
        return (
          <Box
            key={index}
            minWidth="20%"
            border={
              index === tabI ? "2px solid white" : "0.5px solid transparent"
            }
          >
            <GradientBox
              className={cn({ "p-2": !artP }, "pointer")}
              bgImg={artP ? artP.rarity || 5 : 1}
              onClick={() => handleClick(index)}
            >
              <img className="full-w" src={src} alt={type} draggable={false} />
            </GradientBox>
          </Box>
        );
      })}
    </div>
  );
}
