import { useDispatch, useSelector } from "react-redux";
import {
  CHANGE_MCS_INPUT,
  TOGGLE_MCS,
  selectATTRs
} from "../../../../app/calculatorSlice";
import { findWeapon } from "../../../../data/controllers";
import { findByIndex } from "../../../../helpers";
import { processNumInp } from "../../../../helpers/processors";
import { Checkbox, TextInput } from "../../../../styledCpns/Inputs";
import { Section, StyledSelect, StyledSetter } from "../CmnCpns";

export default function WpBuffsMain({ weapon }) {
  const ATTRs = useSelector(selectATTRs);
  const dispatch = useDispatch();
  const { type, code, refinement: wpRfm, BCs } = weapon;
  const { name, buffs } = findWeapon({ type, code });

  return BCs.map((ctrl, bcIndex) => {
    const { activated } = ctrl;
    const buff = findByIndex(buffs, ctrl.index);
    return (
      <Section
        key={code.toString() + bcIndex}
        checked={activated}
        handleCheck={() =>
          dispatch(
            TOGGLE_MCS({
              cat: "wpRack",
              type: "BCs",
              mcIndex: bcIndex,
              activated: !activated
            })
          )
        }
        heading={name + " (self)"}
        desc={buff.desc({ wpRfm, ATTRs })}
        setters={
          <SetterSection buff={buff} inputs={ctrl.inputs} bcIndex={bcIndex} />
        }
      />
    );
  });
}

function SetterSection({ buff, inputs, bcIndex }) {
  const dispatch = useDispatch();
  if (!buff.labels) return null;
  const path = (inpIndex) => ({
    cat: "wpRack",
    type: "BCs",
    mcIndex: bcIndex,
    inpIndex
  });
  return buff.labels.map((label, i) => {
    const type = buff.inputTypes[i];
    let inputCpn;
    switch (type) {
      case "stacks":
        const options = [...Array(buff.values[i])];
        if (buff.values[i] === 0) {
          options.unshift(0);
        }
        inputCpn = (
          <StyledSelect
            value={inputs[i]}
            onChange={(e) =>
              dispatch(CHANGE_MCS_INPUT({ ...path(i), value: +e.target.value }))
            }
          >
            {options.map((_, opt) => (
              <option key={opt}>{opt + 1}</option>
            ))}
          </StyledSelect>
        );
        break;
      case "check":
        inputCpn = (
          <Checkbox
            checked={inputs[i]}
            onChange={() =>
              dispatch(CHANGE_MCS_INPUT({ ...path(i), value: !inputs[i] }))
            }
          />
        );
        break;
      case "choices":
        inputCpn = (
          <StyledSelect
            value={inputs[i]}
            onChange={(e) =>
              dispatch(CHANGE_MCS_INPUT({ ...path(i), value: e.target.value }))
            }
          >
            {buff.values[i].map((opt) => (
              <option key={opt}>{opt}</option>
            ))}
          </StyledSelect>
        );
        break;
      case "text":
        inputCpn = (
          <TextInput
            className="px-2 py-2"
            width="4.25rem"
            borderRadius="0.25rem"
            align="right"
            value={inputs[i]}
            onChange={(e) =>
              dispatch(
                CHANGE_MCS_INPUT({
                  ...path(i),
                  value: processNumInp(
                    e.target.value,
                    inputs[i],
                    buff.maxs?.[i]
                  )
                })
              )
            }
          />
        );
        break;
      default:
        inputCpn = null;
    }
    return (
      <StyledSetter key={i}>
        <p className="label">{label}</p>
        {inputCpn}
      </StyledSetter>
    );
  });
}
