import { createSelector } from "@reduxjs/toolkit";
import { useDispatch, useSelector } from "react-redux";
import { selectArt, SET_ART_PIECE } from "../../../app/calculatorSlice";
import { selectMyChars } from "../../../app/databaseSlice";
import { pickEquippedArtSet } from "../../../app/thunks";
import { artTypes } from "../../../configs";
import { findCharacter } from "../../../data/controllers";
import ArtInventory from "../../MyItemsDB/ArtInventory";
import Picker from "../../Picker";

const selArtPieces = createSelector(selectArt, (art) => art.pieces);

export function ArtPicker({ artType, close }) {
  const pieces = useSelector(selArtPieces);
  const dispatch = useDispatch();
  return (
    <ArtInventory
      curPieces={pieces}
      artType={artType}
      close={close}
      btnText="Pick"
      btnClick={({ user, ...info }) =>
        dispatch(
          SET_ART_PIECE({
            pieceIndex: artTypes.indexOf(artType),
            newPiece: info
          })
        )
      }
    />
  );
}

export function CharPicker({ close }) {
  const myChars = useSelector(selectMyChars);
  const dispatch = useDispatch();
  const data = myChars.map(({ name, constellation, artIDs }) => {
    const data = findCharacter({ name }, "code icon rarity vision weapon");
    return { name, constellation, artIDs, ...data };
  });
  return (
    <Picker
      data={data}
      dataType="Character"
      close={close}
      pick={({ artIDs }) => dispatch(pickEquippedArtSet(artIDs))}
    />
  );
}
