import { Gold, Green, Link, Red, Text } from "../../styledCpns/DataDisplay";
import { AbsoluteBox } from "../../styledCpns/Layout";
import { CloseBtn, ExpandableList } from "../CommonCpns";
import Modal from "../CommonCpns/Modal";
import { StyledIntro } from "./styles";
import Updates from "./Updates";

