function About() {
  return (
    <div className="hidden_content">
      <p>
        - Hello, I'm <Gold>Ronqueroc</Gold> the owner of this App. I lost my job
        at a hotel due to the pandemic and started to learn programming on July
        2020.
      </p>
      <p>
        - This App is for calculating DMG a Character in game Genshin Impact can
        do with their attacks in a specific scenario (Setup). It is not
        affiliated with or endorsed by miHoYo.
      </p>
      <p>
        - Feel free to contact me if you encounter bugs or have any questions
        regarding the Calculator.
      </p>
      {contacts.map(({ Icon, href, text }) => (
        <p key={text} className="flex align-center">
          <Icon className="mr-2" size="1.25rem" />
          <Link href={href} rel="noreferrer" target="_blank">
            {text}
          </Link>
        </p>
      ))}
      <p className="pl-6">(yes the email address is real)</p>
      <p>
        - I got my first web dev job on June 6th 2022 and I'm currently very
        busy keeping up with the team. I barely have time to play Genshin
        Impact. Therefore my replies might be late and I might not catch up with
        the news. If you want beta stuffs to be added to the Calculator, you
        might need to send me their information AFTER THE BETA HAS STARTED.
        Thank you.
      </p>
    </div>
  );
}
