import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { selectCharData } from "../../../app/calculatorSlice";
import { getThisChar } from "../../../app/calculatorSlice/helpers";
import { selectMyArts, selectMyWps } from "../../../app/databaseSlice";
import { selectMySetups } from "../../../app/setupsSlice";
import { saveSetup } from "../../../app/thunks";
import { deepCopy, findById, isOne } from "../../../helpers";
import { filterAllMCs } from "../../../helpers/processors";
import { Text } from "../../../styledCpns/DataDisplay";
import { TextInput } from "../../../styledCpns/Inputs";
import { Box } from "../../../styledCpns/Layout";
import { ButtonBar } from "../../CommonCpns";
import Modal from "../../CommonCpns/Modal";
import ShareDialog from "../../CommonCpns/ShareDialog";

export function SaveUtil({ setup: { name, ID, index }, close }) {
  const charData = useSelector(selectCharData);
  const existed = findById(useSelector(selectMySetups), ID);
  const [input, setInput] = useState(
    existed ? existed.name : charData.name + " - " + name
  );
  const data = useProcessData(index, true);
  const dispatch = useDispatch();

  return (
    <Modal close={close}>
      <Box
        className="px-8 py-6 flex-col white-glow-shadow max-width-95"
        width="30rem"
        borderRadius="0.5rem"
        bgColor="cardBg1"
      >
        <Text className="mb-2 mx-auto" variant="h5" color="orange" bold>
          {existed ? "Modify OLD" : "Save NEW"} Setup
        </Text>
        <TextInput
          className="mt-1 mb-8 px-4 pt-4 pb-2"
          type="text"
          borderRadius="0.25rem"
          fontSize="1.5rem"
          align="center"
          value={input}
          onChange={(e) => {
            const { value } = e.target;
            if (value.length <= 34) setInput(value);
          }}
          onKeyDown={(e) => {
            if (e.key === "Enter") {
              dispatch(saveSetup(ID, input, data));
              close();
            }
          }}
        />
        <ButtonBar
          texts={["Cancel", "Confirm"]}
          handlers={[
            close,
            () => {
              dispatch(saveSetup(ID, input, data));
              close();
            }
          ]}
        />
      </Box>
    </Modal>
  );
}

export const SharedUtil = ({ setup, close }) => (
  <ShareDialog
    setupName={setup.name}
    data={useProcessData(setup.index, false)}
    close={close}
  />
);

function useProcessData(index, doCompare) {
  const {
    char,
    selfMCs,
    wpRack,
    artChest,
    subWpMCs,
    parties,
    elmtMCs,
    customMCs,
    target
  } = useSelector((state) => state.calculator);
  const myArts = useSelector(selectMyArts);
  const myWps = useSelector(selectMyWps);
  const thisChar = getThisChar(char, index);
  const art = deepCopy(artChest[index]);
  const weapon = deepCopy(wpRack[index]);
  if (doCompare) {
    let newID = Date.now();
    for (const p of art.pieces) {
      if (!p) continue;
      const existed = findById(myArts, p.ID);
      if (existed) {
        const { user, ...info } = existed;
        if (!isOne(p, info)) {
          p.ID = newID;
          newID++;
        }
      }
    }
    const existed = findById(myWps, weapon.ID);
    if (existed) {
      const { user, ...existedInfo } = existed;
      const { BCs, DCs, ...info } = weapon;
      if (!isOne(info, existedInfo)) {
        weapon.ID = newID;
      }
    }
  }
  return {
    char: thisChar,
    ...filterAllMCs({
      char: thisChar,
      selfMCs: selfMCs[index],
      weapon,
      subWpMCs: subWpMCs[index],
      art,
      party: parties[index],
      elmtMCs: elmtMCs[index]
    }),
    customMCs: customMCs[index],
    target
  };
}
