import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  selectChar,
  selectSetups,
  selectTarget
} from "../../app/calculatorSlice";
import {
  addImportedSetup,
  initSessionWithImportedSetup
} from "../../app/thunks";
import { isOne } from "../../helpers";
import Modal from "../CommonCpns/Modal";
import { useImportedSetup } from "./helpers";
import ImportPending from "./ImportPending";
import { LoadingBar } from "./styles";

export default function Importing() {
  const char = useSelector(selectChar);
  const target = useSelector(selectTarget);
  const numOfSetups = useSelector(selectSetups);
  const [pendingCode, setPendingCode] = useState(0);
  const dispatch = useDispatch();
  const imported = useImportedSetup();

  const importSetup = (overwriteChar, overwriteTg) => {
    dispatch(addImportedSetup(imported, overwriteChar, overwriteTg));
  };

  const startSession = () => dispatch(initSessionWithImportedSetup(imported));

  useEffect(() => {
    const execute = (fn) => setTimeout(fn, 100);
    if (char) {
      if (char.name === imported.char.name) {
        if (numOfSetups === 4) {
          execute(() => setPendingCode(2));
        } else {
          const sameChar = isOne(char, imported.char);
          const sameTg = isOne(target, imported.target);
          if (sameChar && sameTg) {
            execute(() => importSetup(false, false));
          } else {
            let code = 3;
            if (!sameChar) code = code * 10;
            if (!sameTg) code = code * 10 + 1;
            setPendingCode(code);
          }
        }
      } else {
        execute(() => setPendingCode(1));
      }
    } else {
      execute(startSession);
    }
  }, []);

  return !pendingCode ? (
    <Modal>
      <LoadingBar>
        <div />
      </LoadingBar>
    </Modal>
  ) : (
    <ImportPending
      imported={imported}
      code={pendingCode}
      startSession={startSession}
      importSetup={importSetup}
    />
  );
}
