import { useCallback, useState } from "react";
import { FaPlus } from "react-icons/fa";
import { useDispatch, useSelector } from "react-redux";
import { selectCurSetupI, selectSetups } from "../../../app/calculatorSlice";
import {
  getNewSetupName,
  getSetupInfo
} from "../../../app/calculatorSlice/helpers";
import { applySettings } from "../../../app/thunks";
import {
  CLOSE_SETTINGS,
  selectCmpSetupIs,
  selectStdSetupI
} from "../../../app/uiSlice";
import { Text } from "../../../styledCpns/DataDisplay";
import { Button } from "../../../styledCpns/Inputs";
import { AbsoluteBox, Box, Space } from "../../../styledCpns/Layout";
import { colors } from "../../../theme";
import { ButtonBar, CloseBtn, ConfirmDialog } from "../../CommonCpns";
import { HiddenScroll } from "../../CommonCpns/HiddenSpace";
import Configs from "./Configs";
// import ImportPreper from "./ImportPreper";
import SetupCtrl from "./SetupCtrl";
import UpdateDB from "./UpdateDB";
import { SaveUtil, SharedUtil } from "./Utils";

export function ImportBtn() {
  const [impPreping, setImpPreping] = useState(false);
  return (
    <Box height="2rem">
      <Button
        className="full-w full-h glow-on-hover"
        bgColor="#2269d6"
        color="white"
        onClick={() => setImpPreping(true)}
      >
        Import
      </Button>

      {/* {impPreping && <ImportPreper close={() => setImpPreping(false)} />} */}
      {impPreping && (
        <ConfirmDialog
          message="This feature is no longer in service"
          close={() => setImpPreping(false)}
        />
      )}
    </Box>
  );
}

export default function HiddenSettings({ height }) {
  const on = useSelector((state) => state.ui.settingsOn);
  return (
    <AbsoluteBox className="full-w" bottom={0} left={0}>
      <HiddenScroll
        on={on}
        style={{
          borderRadius: "0.625rem",
          backgroundColor: colors.cardBg3
        }}
        openHeight={height / 16 + 2 + "rem"}
        duration={250}
        timingFunc="ease-in-out"
      >
        <Settings />
      </HiddenScroll>
    </AbsoluteBox>
  );
}

function Settings() {
  const setups = useSelector(selectSetups);
  const curSetupI = useSelector(selectCurSetupI);
  const configs = useSelector((state) => state.calculator.configs);
  const stdSetupI = useSelector(selectStdSetupI);
  const cmpSetupIs = useSelector(selectCmpSetupIs);

  const [tempSetups, setTempSetups] = useState(
    setups.map((st, index) => {
      const temp = {
        ...st,
        index,
        checked: cmpSetupIs.includes(index),
        expanded: false
      };
      if (index === stdSetupI) temp.isStandard = true;
      if (index === curSetupI) temp.isCurrent = true;
      return temp;
    })
  );
  const [tempConfigs, setTempConfigs] = useState(configs);
  const [err, setErr] = useState(false);
  const [util, setUtil] = useState({ type: "", index: null });
  const dispatch = useDispatch();

  const closeUtil = () => setUtil({ type: "", index: null });

  const tryApply = useCallback(() => {
    let ok = true;
    const names = [];
    for (const { name } of tempSetups) {
      if (!name.length || names.includes(name)) {
        ok = false;
        break;
      } else names.push(name);
    }
    if (ok) {
      dispatch(applySettings(tempSetups, tempConfigs));
    } else {
      setErr(true);
    }
  }, [tempConfigs, tempSetups, dispatch]);

  const settingsUtils = {
    save: <SaveUtil setup={tempSetups[util.index]} close={closeUtil} />,
    share: <SharedUtil setup={tempSetups[util.index]} close={closeUtil} />,
    updateDB: <UpdateDB index={util.index} close={closeUtil} />
  };
  return (
    <div className="p-4 full-h flex-col">
      <AbsoluteBox top="0.75rem" right="0.75rem">
        <CloseBtn
          className="glow-on-hover"
          onClick={() => dispatch(CLOSE_SETTINGS())}
        />
      </AbsoluteBox>
      <Text className="mt-2" variant="h3" align="center" color="orange">
        SETTINGS
      </Text>
      <Box className="mt-2 grow-1 flex-col hide-sb">
        <Space>
          {tempSetups.map((setup, index) => (
            <SetupCtrl
              key={index}
              setup={setup}
              index={index}
              belowLimit={tempSetups.length < 4}
              setTempSetups={setTempSetups}
              setUtil={setUtil}
            />
          ))}
        </Space>
        {tempSetups.length < 4 && (
          <div className="mt-6">
            <Box className="mb-6" height="2rem">
              <Button
                className="full-h full-w flex-center glow-on-hover"
                bgColor="#2269d6"
                color="white"
                borderRadius="1rem"
                onClick={() => {
                  setTempSetups((prev) => {
                    const newTSs = [...prev];
                    newTSs.push({
                      ...getSetupInfo({ name: getNewSetupName(prev) }),
                      index: null,
                      checked: false,
                      expanded: false
                    });
                    return newTSs;
                  });
                }}
              >
                <FaPlus />
              </Button>
            </Box>
            <ImportBtn />
          </div>
        )}
        <Configs configs={tempConfigs} setConfigs={setTempConfigs} />
        {err && (
          <Text className="mt-4" color="lightRed" align="center">
            <i>Please choose a unique name for each Setup.</i>
          </Text>
        )}
      </Box>
      <ButtonBar
        className="mt-4"
        variants={["positive"]}
        texts={["Apply"]}
        handlers={[tryApply]}
      />
      {settingsUtils[util.type]}
    </div>
  );
}
