import cn from "classnames";
import { useEffect, useState } from "react";
import { FaCaretLeft, FaCaretRight } from "react-icons/fa";
import { findArtifact, findWeapon } from "../../../data/controllers";
import { btnState } from "../../../helpers";
import { Orange, Text } from "../../../styledCpns/DataDisplay";
import { Space } from "../../../styledCpns/Layout";
import { NoItems } from "../Common";
import Thumb from "../Thumb";

function getWpInfo({ type, code, ...rest }) {
  const { beta, name, icon, rarity } = findWeapon({ type, code });
  return { beta, name, icon, rarity, ...rest };
}
function getArtInfo({ code, type, user, rarity, level }) {
  const { beta, name, icon } = findArtifact({ code, type });
  return { beta, name, icon, rarity, level, user };
}

const itemLimit = 120;

export default function useInvRack({ items, itemType, filteredIds }) {
  const [chosenID, setChosenID] = useState(0);
  const [clickedID, setClickedID] = useState(null);
  const [pageNo, setPageNo] = useState(0);

  const deadEnd = Math.ceil(filteredIds.length / itemLimit) - 1;
  const getInfo = itemType === "Weapon" ? getWpInfo : getArtInfo;

  let IDsOnPage;
  if (filteredIds.length > itemLimit) {
    IDsOnPage = [];
    let i = itemLimit * pageNo;
    while (IDsOnPage.length < itemLimit && filteredIds[i]) {
      IDsOnPage.push(filteredIds[i]);
      i++;
    }
  } else {
    IDsOnPage = filteredIds;
  }

  const navigateMaker = (available, step) => () => {
    if (available) setPageNo((prev) => prev + step);
  };
  const goBack = navigateMaker(pageNo > 0, -1);
  const goNext = navigateMaker(pageNo < deadEnd, 1);

  // update chosenID after filter
  useEffect(() => {
    if (chosenID !== IDsOnPage[0] && !IDsOnPage.includes(chosenID)) {
      setChosenID(IDsOnPage[0]);
    }
  }, [IDsOnPage, chosenID]);

  // hop back when fewer result after filter
  useEffect(() => {
    if (pageNo > deadEnd && deadEnd > 0) setPageNo(deadEnd);
  }, [pageNo, deadEnd]);

  // add navigate with arrow keys
  useEffect(() => {
    const navigateWithArrow = (e) => {
      if (e.key === "ArrowLeft") goBack();
      else if (e.key === "ArrowRight") goNext();
    };
    document.addEventListener("keydown", navigateWithArrow);
    return () => document.removeEventListener("keydown", navigateWithArrow);
  }, [goBack, goNext]);

  let rackContent;

  if (!filteredIds.length) {
    rackContent = <NoItems type={itemType} />;
  } else {
    rackContent = (
      <div className="flex-wrap">
        {items.map((item) => {
          const { ID } = item;
          return (
            <div
              key={ID}
              className={cn("cell", { "d-none": !IDsOnPage.includes(ID) })}
              onDoubleClick={() => console.log(item)}
            >
              <Thumb
                item={getInfo(item)}
                chosen={ID === chosenID}
                clicked={ID === clickedID}
                handleMouseDown={() => setClickedID(ID)}
                choose={() => {
                  setClickedID(null);
                  setChosenID(ID);
                }}
              />
            </div>
          );
        })}
      </div>
    );
  }
  return [
    <div className="flex-col full-w">
      <div className="rack hide-sb">{rackContent}</div>
      {IDsOnPage.length ? (
        <Space className="pt-2 flex-center" row gap="0.5rem">
          <FaCaretLeft
            className={btnState(pageNo > 0)}
            size="1.75rem"
            onClick={goBack}
          />
          <Text bold>
            <Orange>{pageNo + 1}</Orange> / {deadEnd + 1}
          </Text>
          <FaCaretRight
            className={btnState(pageNo < deadEnd)}
            size="1.75rem"
            onClick={goNext}
          />
        </Space>
      ) : null}
    </div>,
    chosenID,
    setChosenID
  ];
}
