import { createSelector } from "@reduxjs/toolkit";
import cn from "classnames";
import { useState } from "react";
import { FaCircle } from "react-icons/fa";
import { useDispatch, useSelector } from "react-redux";
import { getThisChar } from "../../../app/calculatorSlice/helpers";
import { updateDbAndSetups } from "../../../app/thunks";
import { artifactIcons, artTypes, pctOf, wikiImg } from "../../../configs";
import { artMS } from "../../../data/artifacts/config";
import { findArtifact, findWeapon } from "../../../data/controllers";
import { extractData, findById, findByName, isOne } from "../../../helpers";
import { Table, Text, Tooltip } from "../../../styledCpns/DataDisplay";
import { AbsoluteBox, Box, Space } from "../../../styledCpns/Layout";
import { ButtonBar, ConfirmDialog, InfoSign } from "../../CommonCpns";
import Modal from "../../CommonCpns/Modal";

export default function PreCheck({ index, close }) {
  const [done, setDone] = useState(false);
  return done ? (
    <ConfirmDialog message="Succeeded!" close={close} />
  ) : (
    <UpdateDB index={index} close={close} setDone={setDone} />
  );
}

const actions = ["No change", "Add", "Update"];

const getHeadings = (actionI) =>
  actionI === 2 ? ["Old", "New"] : actionI === 1 ? ["New"] : ["Old"];

function getActionI(char, item, dbItem) {
  if (!dbItem) return 1;
  const { user, ...dbInfo } = dbItem;
  return isOne(item, dbInfo) && user === char.name ? 0 : 2;
}

const selDB = createSelector(
  (state) => state.calculator.char,
  (state) => state.calculator.artChest,
  (state) => state.calculator.wpRack,
  (_, index) => index,
  (char, artChest, wpRack, index) => {
    const { BCs, DCs, ...wpInfo } = wpRack[index];
    return [getThisChar(char, index), wpInfo, artChest[index].pieces];
  }
);

function UpdateDB({ index, close, setDone }) {
  const [char, wp, artPs] = useSelector((state) => selDB(state, index));
  const [dbChar, dbWp, dbArtPs] = useSelector((state) => {
    const { myChars, myWps, myArts } = state.database;
    const existed = findByName(myChars, char.name);
    let charInfo;
    if (existed) {
      const { weaponID, artIDs, ...info } = existed;
      charInfo = info;
    }
    return [
      charInfo,
      findById(myWps, wp.ID),
      artPs.map((p) => p?.ID && findById(myArts, p.ID))
    ];
  });
  const dispatch = useDispatch();

  const actionIs = [
    dbChar ? (isOne(char, dbChar) ? 0 : 2) : 1,
    getActionI(char, wp, dbWp),
    []
  ];
  dbArtPs.forEach((p, i) => {
    let actI;
    if (!p && !artPs[i]) {
      actI = 0;
    } else {
      actI = getActionI(char, artPs[i], p);
    }
    actionIs[2].push(actI);
  });
  const canApply = actionIs[0] || actionIs[1] || actionIs[2].some((i) => i);
  return (
    <Modal close={close}>
      <Box
        className="p-4 max-width-95 white-glow-shadow"
        borderRadius="0.5rem"
        bgColor="cardBg3"
      >
        <AbsoluteBox
          className="pt-2 tooltip-wrapper"
          bottom="0.75rem"
          left="0.75rem"
          zIndex={1}
        >
          <InfoSign />
          <Tooltip
            className="tooltip"
            bottom="100%"
            left={0}
            style={{ width: "18rem" }}
          >
            <Text>
              - Adds Character / Items if they are not in your database,
              otherwise updates if there is a change.
            </Text>
            <Text>
              - Takes Items from or swaps Items with other Characters if
              necessary.
            </Text>
            <Text color="lightRed">
              - Also updates Items used in your saved Setups.
            </Text>
            <Text>
              - Explanation: Pick an Item from database {">"} Save Setup {">"}{" "}
              Change the Item and use this utility {">"} The Item is updated in
              My Weapons / My Artifacts and in the save Setup.
            </Text>
          </Tooltip>
        </AbsoluteBox>
        <Space className="custom-sb" row>
          <Reporter
            title="Character"
            actionI={actionIs[0]}
            headings={getHeadings(actionIs[0])}
            obj1={actionIs[0] === 2 ? dbChar : char}
            obj2={actionIs[0] === 2 && char}
          />
          <WpReporter char={char} actionI={actionIs[1]} wp={wp} dbWp={dbWp} />
          <ArtReporter
            char={char}
            actionIs={actionIs[2]}
            artPs={artPs}
            dbArtPs={dbArtPs}
          />
        </Space>
        <ButtonBar
          className="mt-4"
          texts={["Cancel", "Apply"]}
          availables={[true, canApply]}
          handlers={[
            close,
            () => {
              if (canApply) {
                dispatch(updateDbAndSetups([char, wp, artPs, actionIs]));
                setDone(true);
              }
            }
          ]}
        />
      </Box>
    </Modal>
  );
}

function WpReporter({ char, actionI, wp, dbWp }) {
  let obj1, obj2;
  const getWpObj = (weapon, name) => ({
    name: findWeapon(weapon).name,
    ...extractData(weapon, "level refinement"),
    user: weapon.user || name
  });
  if (actionI) {
    if (actionI === 2) {
      [obj1, obj2] = [getWpObj(dbWp), getWpObj(wp, char.name)];
    } else {
      obj1 = getWpObj(wp, char.name);
    }
  }
  return (
    <Reporter
      title="Weapon"
      actionI={actionI}
      headings={getHeadings(actionI)}
      obj1={obj1}
      obj2={obj2}
    />
  );
}

function ArtReporter({ char, actionIs, artPs, dbArtPs }) {
  const [openI, setOpenI] = useState(0);
  const actionI = actionIs[openI];
  let obj1, obj2;
  const getPieceObj = (piece, name) => {
    const { level, mainSType } = piece;
    const result = {
      set: findArtifact({ code: piece.code }).name,
      level,
      [mainSType]:
        artMS[piece.type][mainSType][piece.rarity || 5][level] +
        pctOf(mainSType)
    };
    for (const { type, val } of piece.subS) {
      result[type] = val + pctOf(type);
    }
    result.user = piece.user || name;
    return result;
  };
  if (actionI) {
    if (actionI === 2) {
      [obj1, obj2] = [
        getPieceObj(dbArtPs[openI]),
        getPieceObj(artPs[openI], char.name)
      ];
    } else {
      obj1 = getPieceObj(artPs[openI], char.name);
    }
  }
  return (
    <Box minWidth="18rem" height="70vh">
      <Text variant="h3" color="orange">
        Artifacts
      </Text>
      <div className="mt-2 flex">
        {artTypes.map((type, i) => (
          <Box
            key={type}
            className="p-2 relative"
            minWidth="20%"
            borderRadius="50%"
            bgColor={i === openI ? "green" : "transparent"}
            onClick={() => setOpenI(i)}
          >
            <img className="full-w" src={wikiImg(artifactIcons[type])} alt="" />
            {actionIs[i] ? (
              <AbsoluteBox className="flex-center" top={0} right={0} zIndex={1}>
                <FaCircle color="red" />
              </AbsoluteBox>
            ) : null}
          </Box>
        ))}
      </div>
      <Text className="mt-1">{actions[actionIs[openI]]}</Text>
      {actionI ? (
        <ReportTable
          headings={getHeadings(actionIs[openI])}
          obj1={obj1}
          obj2={obj2}
        />
      ) : null}
    </Box>
  );
}

const ReportTable = ({ headings, obj1, obj2 }) => (
  <Table className="mt-2">
    <tbody>
      <tr>
        <th></th>
        {headings.map((h) => (
          <th key={h} className="lightGold-text">
            {h}
          </th>
        ))}
      </tr>
      {Object.keys(obj1).map((type, i) => (
        <tr key={i}>
          <td
            className={cn("capitalize", {
              warning: obj2 && obj1[type] !== obj2[type]
            })}
          >
            {type}
          </td>
          <td>{obj1[type]}</td>
          {obj2 && <td>{obj2[type]}</td>}
        </tr>
      ))}
    </tbody>
  </Table>
);

function Reporter({ title, actionI, headings, obj1, obj2 }) {
  return (
    <Box minWidth="18rem" height="70vh">
      <Text variant="h3" color="orange">
        {title}
      </Text>
      <Text className="mt-1">{actions[actionI]}</Text>
      {actionI ? (
        <ReportTable headings={headings} obj1={obj1} obj2={obj2} />
      ) : null}
    </Box>
  );
}
