import cn from "classnames";
import {
  FaCaretDown,
  FaCaretLeft,
  FaCaretRight,
  FaCaretUp,
  FaQuestion
} from "react-icons/fa";
import styled from "styled-components";
import { wikiImg } from "../../configs";
import { Img } from "../../styledCpns/DataDisplay";
import { Box } from "../../styledCpns/Layout";
import { colors } from "../../theme";

export const SharedSpace = styled.div`
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  .right_part,
  .left_part {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    transition: 200ms linear;
  }
  .left_part.close {
    right: 100%;
  }
  .left_part.open {
    right: 0;
  }
  .right_part.close {
    left: 100%;
  }
  .right_part.open {
    left: 0;
  }
  .cons_right_part {
    border-top: 3px solid ${colors.cardBg2};
    border-bottom: 3px solid ${colors.cardBg2};
  }
`;

const gradient = {
  Pyro: "2, 100%, 65%",
  Hydro: "204, 100%, 59%",
  Cryo: "182, 100%, 73%",
  Electro: "278, 100%, 73%",
  Anemo: "164, 100%, 64%",
  Geo: "39, 100%, 67%",
  Dendro: "116, 72%, 56%"
};

const UnknownIcon = styled(({ size, vision, className, ...rest }) => (
  <div className={cn(className, "flex-center")} {...rest}>
    <FaQuestion size="1.25rem" />
  </div>
))`
  min-width: ${(pr) => pr.size || "3.25rem"};
  height: ${(pr) => pr.size || "3.25rem"};
  border-radius: 50%;
  background-color: ${(pr) => colors[pr.vision]};
  background: radial-gradient(
    circle,
    hsla(0, 0%, 0%, 1) 20%,
    hsla(${(pr) => gradient[pr.vision]}, 1) 100%
  );
  background: -moz-radial-gradient(
    circle,
    hsla(0, 0%, 0%, 1) 20%,
    hsla(${(pr) => gradient[pr.vision]}, 1) 100%
  );
  background: -webkit-radial-gradient(
    circle,
    hsla(0, 0%, 0%, 1) 20%,
    hsla(${(pr) => gradient[pr.vision]}, 1) 100%
  );
  transition: opacity ease-out 150ms;
`;

export const ConsAndTlImg = ({ img, size = "3.25rem", vision, ...rest }) => {
  return img ? (
    <Img width={size} src={wikiImg(img)} alt="" draggable={false} {...rest} />
  ) : (
    <UnknownIcon size={size} vision={vision} {...rest} />
  );
};

const StyledSlideShow = styled.div`
  position: absolute;
  top: 0;
  left: ${(props) => `-${props.leftDist * 3.5}rem`};
  display: flex;
  transition: left 100ms linear;
`;
export function SlideShow(props) {
  const { index, images, vision } = props;
  return (
    <div
      className={cn(
        props.forTl ? "pt-1 pb-2" : "pt-2 pb-4",
        "flex-center relative"
      )}
    >
      {props.typeCpn}
      <div className="relative">
        <Box
          className="overflow-hidden relative"
          width="3.5rem"
          height="3.5rem"
        >
          <StyledSlideShow leftDist={index}>
            {images.map((img, i) => (
              <ConsAndTlImg key={i} img={img} size="3.5rem" vision={vision} />
            ))}
          </StyledSlideShow>
        </Box>
        {index > 0 && <Caret dir="left" onClick={props.back} />}
        {index < images.length - 1 && (
          <Caret dir="right" onClick={props.next} />
        )}
      </div>
    </div>
  );
}

const StyledCaret = styled.p`
  position: absolute;
  top: 0;
  font-size: 2.5rem;
  color: ${colors.cardBg3};
  &:hover {
    color: ${colors.lightGold};
  }
  &.left,
  &.right {
    top: 0.5rem;
  }
  &.left {
    right: 100%;
  }
  &.right {
    left: 100%;
  }
`;
const Caret = ({ dir, onClick }) => {
  const Icon = dir === "right" ? FaCaretRight : FaCaretLeft;
  return (
    <StyledCaret
      className={
        (dir === "right" ? "pl-4 pr-2 right" : "pl-2 pr-4 left") +
        " flex-center pointer"
      }
      onClick={onClick}
    >
      <Icon />
    </StyledCaret>
  );
};

export const LvBtn = styled(({ dir, ...others }) => {
  const Icon = dir === "up" ? FaCaretUp : FaCaretDown;
  return (
    <div {...others}>
      <Icon />
    </div>
  );
})`
  display: flex;
  padding: 0 0.5rem;
  border-radius: 0.25rem;
  border-width: 3px;
  border-style: solid;
  border-color: ${colors.cardBg3};
  color: ${colors.cardBg3};
  font-size: 1.375rem;
  cursor: pointer;
  :hover {
    border-color: ${colors.green};
    color: ${colors.green};
  }
`;
