import { useDispatch, useSelector } from "react-redux";
import {
  CHANGE_ELMT_MCS,
  CHANGE_MCS_INPUT,
  CHANGE_RESONANCE_INPUT,
  selectArt,
  selectATTRs,
  selectChar,
  selectCharData,
  selectElmtMCs,
  selectInfusion,
  selectRxnBnes,
  TOGGLE_MCS,
  TOGGLE_RESONANCE
} from "../../../../app/calculatorSlice";
import { getQuickenBuffDamage } from "../../../../calculators/helpers";
import { ampElmts, crystallizables, rsnBuffDesc } from "../../../../configs";
import { findArtBuff, findArtifact } from "../../../../data/controllers";
import { Green, Span } from "../../../../styledCpns/DataDisplay";
import { Checkbox } from "../../../../styledCpns/Inputs";
import { AmpRxnDesc } from "../../../CommonCpns";
import { NoContent, Section, StyledSelect, StyledSetter } from "../CmnCpns";

const dendroRsnInputLabels = [
  "Trigger Burning / Catalyze / Bloom",
  "Trigger Aggravate / Spread / Hyperbloom / Burgeon"
];

export function ElmtBuffs() {
  const { vision } = useSelector(selectCharData);
  const elmtMCs = useSelector(selectElmtMCs);
  const infusion = useSelector(selectInfusion).NA;
  const dispatch = useDispatch();
  const content = [];

  elmtMCs.resonance.forEach((rsn, rsnIndex) => {
    let setters;
    if (rsn.name === "Sprawling Greenery") {
      setters = rsn.inputs.map((input, i) => {
        return (
          <StyledSetter key={i}>
            <p className="label">{dendroRsnInputLabels[i]}</p>
            <Checkbox
              checked={input}
              onChange={() => {
                dispatch(
                  CHANGE_RESONANCE_INPUT({
                    name: rsn.name,
                    inpIndex: i,
                    value: !input
                  })
                );
              }}
            />
          </StyledSetter>
        );
      });
    }
    content.push(
      <Section
        key={rsnIndex}
        checked={rsn.activated}
        handleCheck={() =>
          dispatch(TOGGLE_RESONANCE({ rsnIndex, activated: !rsn.activated }))
        }
        heading={rsn.name}
        desc={rsnBuffDesc[rsn.name]}
        setters={setters}
      />
    );
  });
  if (ampElmts.includes(vision)) {
    content.push(
      {
        Pyro: ["Melt", "Vaporize"].map((rxn) => (
          <RxnBuff key={rxn} rxn={rxn} vision={vision} />
        )),
        Hydro: <RxnBuff key="Vaporize" rxn="Vaporize" vision={vision} />,
        Cryo: <RxnBuff key="Melt" rxn="Melt" vision={vision} />
      }[vision]
    );
  }
  if (ampElmts.includes(infusion) && infusion !== vision) {
    content.push(
      {
        Pyro: ["Melt", "Vaporize"].map((rxn) => (
          <NaRxnBuff key={rxn} rxn={rxn} infusion={infusion} />
        )),
        Hydro: <NaRxnBuff key="Vaporize" rxn="Vaporize" infusion={infusion} />,
        Cryo: <NaRxnBuff key="Melt" rxn="Melt" infusion={infusion} />
      }[infusion]
    );
  }
  if (["Electro", "Dendro"].includes(vision)) {
    content.push(<QuickenBuff key={vision} vision={vision} />);
  }

  return content.length ? content : <NoContent type="buffs" />;
}

function QuickenBuff({ vision }) {
  const char = useSelector(selectChar);
  const rxnBnes = useSelector(selectRxnBnes);
  const ATTRs = useSelector(selectATTRs);

  const rxn = vision === "Electro" ? "aggravate" : "spread";
  const quickenBuff = getQuickenBuffDamage(
    char,
    ATTRs?.["Elemental Mastery"] || 0,
    rxnBnes
  );
  const activated = useSelector(selectElmtMCs)[rxn];
  const dispatch = useDispatch();

  const heading =
    rxn === "spread"
      ? "Spread (Dendro on Quicken)"
      : "Aggravate (Electro on Quicken)";

  return (
    <Section
      checked={activated}
      handleCheck={() =>
        dispatch(CHANGE_ELMT_MCS({ type: rxn, value: !activated }))
      }
      heading={heading}
      desc={
        <>
          Increase base <Span color={vision}>{vision} DMG</Span> by{" "}
          <Green b>{quickenBuff[rxn]}</Green>.
        </>
      }
    />
  );
}

function RxnBuff({ rxn, vision }) {
  const rxnBnes = useSelector(selectRxnBnes);
  const activated = useSelector(selectElmtMCs).ampRxn === rxn;
  const dispatch = useDispatch();
  return (
    <Section
      checked={activated}
      handleCheck={() =>
        dispatch(
          CHANGE_ELMT_MCS({ type: "ampRxn", value: activated ? null : rxn })
        )
      }
      heading={rxn}
      desc={<AmpRxnDesc elmt={vision} mult={rxnBnes[rxn]} />}
    />
  );
}

function NaRxnBuff({ rxn, infusion }) {
  const rxnBnes = useSelector(selectRxnBnes);
  const activated = useSelector(selectElmtMCs).naAmpRxn === rxn;
  const dispatch = useDispatch();
  return (
    <Section
      checked={activated}
      handleCheck={() =>
        dispatch(
          CHANGE_ELMT_MCS({ type: "naAmpRxn", value: activated ? null : rxn })
        )
      }
      heading={rxn + " (external infusion)"}
      desc={<AmpRxnDesc elmt={infusion} mult={rxnBnes["na" + rxn]} />}
    />
  );
}

export function ArtBuffs() {
  const { sets, BCs, subBCs } = useSelector(selectArt);
  const dispatch = useDispatch();
  const content = [];

  const mainCode = sets[0]?.code;
  BCs.forEach((ctrl, bcIndex) => {
    const { activated } = ctrl;
    const buff = findArtBuff({ code: mainCode }, ctrl.index);
    const path = {
      cat: "artChest",
      type: "BCs",
      mcIndex: bcIndex
    };
    content.push(
      <Section
        key={mainCode.toString() + bcIndex}
        checked={activated}
        handleCheck={() =>
          dispatch(TOGGLE_MCS({ ...path, activated: !activated }))
        }
        heading={findArtifact({ code: mainCode }).name + " (self)"}
        desc={buff.desc()}
        setters={<SetterSection buff={buff} inputs={ctrl.inputs} path={path} />}
      />
    );
  });
  subBCs.forEach((ctrl, bcIndex) => {
    const { code, activated } = ctrl;
    const buff = findArtBuff({ code }, ctrl.index);
    const path = {
      cat: "artChest",
      type: "subBCs",
      mcIndex: bcIndex
    };
    content.push(
      <Section
        key={code.toString() + bcIndex}
        checked={activated}
        handleCheck={() =>
          dispatch(TOGGLE_MCS({ ...path, activated: !activated }))
        }
        heading={findArtifact({ code }).name}
        desc={buff.desc()}
        setters={<SetterSection buff={buff} inputs={ctrl.inputs} path={path} />}
      />
    );
  });
  return content.length ? content : <NoContent type="buffs" />;
}

function SetterSection({ buff, inputs, path }) {
  const dispatch = useDispatch();
  if (!buff.labels) return null;
  return buff.labels.map((label, i) => {
    const isStacks = buff.inputTypes[i] === "stacks";
    let options = isStacks ? [...Array(buff.maxs[i]).keys()] : crystallizables;
    if (isStacks) {
      if (buff.startsAt0?.[i]) {
        options.push(buff.maxs[i]);
      } else {
        options = options.map((n) => n + 1);
      }
    }
    return (
      <StyledSetter key={i}>
        <p className="label">{label}</p>
        <StyledSelect
          value={inputs[i]}
          onChange={(e) => {
            const { value } = e.target;
            dispatch(
              CHANGE_MCS_INPUT({
                ...path,
                inpIndex: i,
                value: isNaN(value) ? value : +value
              })
            );
          }}
        >
          {options.map((opt) => (
            <option key={opt}>{opt}</option>
          ))}
        </StyledSelect>
      </StyledSetter>
    );
  });
}
