import { batch } from "react-redux";
import { findById } from "../helpers";
import {
  APPLY_SETTINGS_CALC,
  IMPORT_SETUP,
  INIT_SESSION_WITH_CHAR,
  INIT_SESSION_WITH_SETUP,
  SET_ALL_ART_PIECES
} from "./calculatorSlice";
import { CALCULATOR_UPDATE_DB } from "./databaseSlice";
import { CALCULATOR_UPDATE_SETUPS, SAVE_SETUP } from "./setupsSlice";
import {
  APPLY_SETTINGS_UI,
  CHANGE_TAB,
  CLOSE_SETTINGS,
  END_IMPORT,
  RESET_CALC_UI,
  TO_IMPORTED
} from "./uiSlice";

export const pickEquippedArtSet = (artIDs) => (dispatch, getState) => {
  const { myArts } = getState().database;
  const artPieces = artIDs.map((id) => {
    if (id) {
      const { user, ...info } = findById(myArts, id);
      return info;
    }
    return null;
  });
  dispatch(SET_ALL_ART_PIECES(artPieces));
};

export const initSessionWithChar = (pickedChar) => (dispatch, getState) => {
  const { myWps, myArts } = getState().database;
  batch(() => {
    dispatch(INIT_SESSION_WITH_CHAR({ pickedChar, myWps, myArts }));
    dispatch(RESET_CALC_UI());
  });
};

export const applySettings = (tempSetups, tempConfigs) => (dispatch) => {
  const setups = [];
  const cmpSetupIs = [];
  const indexes = [];
  let standardI;
  let currentI = -1;
  for (const i in tempSetups) {
    const st = tempSetups[i];
    const { name, ID, type } = tempSetups[i];
    setups.push({ name, ID, type });
    indexes.push(st.index);
    if (st.checked) cmpSetupIs.push(+i);
    if (st.isStandard) standardI = +i;
    if (st.isCurrent) currentI = +i;
  }
  batch(() => {
    dispatch(
      APPLY_SETTINGS_CALC({
        setups,
        indexes,
        tempConfigs,
        currentI,
        standardI
      })
    );
    dispatch(APPLY_SETTINGS_UI({ cmpSetupIs, standardI }));
  });
};

export const addImportedSetup = (imported, overwriteChar, overwriteTg) => (
  dispatch
) => {
  batch(() => {
    dispatch(IMPORT_SETUP({ imported, overwriteChar, overwriteTg }));
    dispatch(END_IMPORT());
    dispatch(TO_IMPORTED());
  });
};

export const initSessionWithImportedSetup = (imported) => (dispatch) => {
  batch(() => {
    dispatch(INIT_SESSION_WITH_SETUP(imported));
    dispatch(END_IMPORT());
    dispatch(RESET_CALC_UI());
  });
};

export const saveSetup = (ID, input, data) => (dispatch) => {
  batch(() => {
    dispatch(SAVE_SETUP({ ID, name: input, data }));
    dispatch(CHANGE_TAB("Setups"));
    dispatch(CLOSE_SETTINGS());
  });
};

export const updateDbAndSetups = ([char, ...rest]) => (dispatch) => {
  batch(() => {
    dispatch(CALCULATOR_UPDATE_DB([char, ...rest]));
    dispatch(CALCULATOR_UPDATE_SETUPS(rest));
  });
};
