import cn from "classnames";
import { memo } from "react";
import { naIcons } from "../../../configs";
import { getPartyData } from "../../../data/controllers";
import { getTotalXtraTlLv } from "../../../helpers";
import { Text } from "../../../styledCpns/DataDisplay";
import { Select } from "../../../styledCpns/Inputs";
import { Space } from "../../../styledCpns/Layout";
import { InfoSign } from "../../CommonCpns";
import { ConsAndTlImg } from "../styles";

export default memo(Overview);

function Overview({ char, charData, party, upgradeTl, toDetails }) {
  return (
    <div className="full-h hide-sb">
      <Space>
        {charData.actvTalents.map((tl, i) => (
          <ActvTalent
            key={i}
            char={char}
            tlData={tl}
            party={party}
            vision={charData.vision}
            weapon={charData.weapon}
            tlLevel={char[tl.type]}
            upgradeTl={(e) => upgradeTl(tl.type, +e.target.value)}
            toDetails={() => toDetails(i)}
          />
        ))}
      </Space>
      {charData.pasvTalents.map((tl, i) => {
        const n = tl.type.slice(-1);
        return (
          <PasvTalent
            key={i}
            vision={charData.vision}
            tlData={tl}
            available={cn({ unavailable: !isNaN(n) && char.ascension < +n })}
            toDetails={() => toDetails(charData.actvTalents.length + i)}
          />
        );
      })}
    </div>
  );
}

function ActvTalent(props) {
  const { tlData, char, weapon, vision, party } = props;
  const partyData = party ? getPartyData(party) : null;
  const xtraLv = getTotalXtraTlLv(char, tlData, partyData);
  return (
    <div className="flex align-center">
      <ConsAndTlImg
        className="my-2 mr-2"
        img={
          tlData.type === "Normal Attack"
            ? naIcons[weapon + vision]
            : tlData.image
        }
        vision={vision}
      />
      <div className="grow-1 flex align-center">
        <div className="px-2">
          <Text bold>{tlData.name}</Text>
          <div className="flex align-center">
            <Text className="mr-1">Lv.</Text>
            {tlData.type === "Alternate Sprint" ? (
              <Text className="ml-1" color={vision} bold>
                1
              </Text>
            ) : (
              <Select
                color={vision}
                bold
                value={props.tlLevel}
                onChange={props.upgradeTl}
              >
                {[...Array(10).keys()].map((_, i) => (
                  <option key={i}>{i + 1}</option>
                ))}
              </Select>
            )}
            {Boolean(xtraLv) && <TalentXtraLv xtraLv={xtraLv} />}
          </div>
        </div>
        <div className="ml-auto" onClick={props.toDetails}>
          <InfoSign selfHover={true} />
        </div>
      </div>
    </div>
  );
}

const TalentXtraLv = ({ xtraLv }) =>
  xtraLv ? (
    <Text className="ml-2" bold color="green">
      +{xtraLv}
    </Text>
  ) : null;

function PasvTalent(props) {
  const { tlData, available, vision } = props;
  return (
    <div className="mt-4 flex align-center">
      <ConsAndTlImg
        className={cn("my-2 mr-2", available)}
        img={tlData.image}
        vision={vision}
      />
      <div className="grow-1 flex align-center">
        <div className={cn("px-2", available)}>
          <Text bold>{tlData.name}</Text>
          <div className="flex">
            <Text className="mr-2">Lv.</Text>
            <Text color={vision} bold>
              1
            </Text>
          </div>
        </div>
        <div className="ml-auto" onClick={props.toDetails}>
          <InfoSign selfHover={true} />
        </div>
      </div>
    </div>
  );
}
