import { useCallback, useState } from "react";
import { FaCog } from "react-icons/fa";
import { useDispatch, useSelector } from "react-redux";
import styled from "styled-components";
import {
  CHANGE_CUR_SETUP,
  PICK_WP_IN_DB,
  selectCharData,
  selectCurSetupI,
  selectSetups
} from "../../../app/calculatorSlice";
import {
  CHANGE_STD_SETUP,
  OPEN_SETTINGS,
  selectCmpSetupIs,
  selectStdSetupI
} from "../../../app/uiSlice";
import { artifactIcons, wikiImg } from "../../../configs";
import { btnState, indexByName } from "../../../helpers";
import { useGetHeight } from "../../../helpers/hooks";
import { Img } from "../../../styledCpns/DataDisplay";
import { Button, IconBtn } from "../../../styledCpns/Inputs";
import { Box } from "../../../styledCpns/Layout";
import { colors } from "../../../theme";
import { ButtonBar } from "../../CommonCpns";
import WpInventory from "../../MyItemsDB/WpInventory";
import PrePicker from "../../Picker/PrePicker";
import Settings, { ImportBtn } from "../Settings";
import { Card, MainSelect } from "../styles";
import { ArtPicker, CharPicker } from "./Pickers";
import SectionArts from "./SectionArts";
import SectionParty from "./SectionParty";
import SectionWp from "./SectionWp";

const Icon = styled(({ className, ...rest }) => (
  <div className={className + " pointer"}>
    <Img width="2.5rem" height="2.5rem" {...rest} draggable={false} />
  </div>
))`
  border-radius: 50%;
  &:hover {
    background-color: ${colors.lightGold};
  }
`;

export default function PreCheck() {
  const charData = useSelector(selectCharData);
  if (!charData) {
    return (
      <Card className="p-4" bgColor="cardBg3">
        <ImportBtn />
      </Card>
    );
  }
  return <SetupManager charData={charData} />;
}

function SetupManager({ charData }) {
  const setups = useSelector(selectSetups);
  const cmpIs = useSelector(selectCmpSetupIs);
  const curI = useSelector(selectCurSetupI);
  const chosen = useSelector(selectStdSetupI) === curI;
  const [modal, setModal] = useState(null);
  const [prePickerOn, setPrePickerOn] = useState(false);
  const [ref, height] = useGetHeight();
  const dispatch = useDispatch();

  const closeInv = useCallback(() => setModal(null), []);

  return (
    <Card
      ref={ref}
      className="p-4 flex-col relative overflow-hidden"
      bgColor="cardBg3"
    >
      <MainSelect
        tab={setups[curI].name}
        setTab={(name) => dispatch(CHANGE_CUR_SETUP(indexByName(setups, name)))}
        options={setups.map((st) => st.name)}
      />
      <div id="setupManager_body" className="mt-4 grow-1 hide-sb scroll-smooth">
        <SectionParty />
        <SectionWp />
        <SectionArts />
      </div>
      <div className="mt-4 flex align-center">
        {cmpIs.length === 1 ? (
          <Box width="5.425rem" />
        ) : (
          <Button
            className={btnState(!chosen)}
            variant="positive"
            onClick={() => {
              if (!chosen) dispatch(CHANGE_STD_SETUP(curI));
            }}
          >
            Choose
          </Button>
        )}
        <IconBtn
          className="mx-auto glow-on-hover"
          fontSize="1.25rem"
          variant="positive"
          onClick={() => dispatch(OPEN_SETTINGS())}
        >
          <FaCog />
        </IconBtn>
        <div className="flex">
          <Icon
            src={wikiImg("7/7b/Icon_Inventory_Weapons")}
            alt="weapons"
            onClick={() => setModal("weapons")}
          />
          <Icon
            src={wikiImg("6/6a/Icon_Inventory_Artifacts")}
            alt="artifacts"
            onClick={() => setPrePickerOn(true)}
          />
        </div>
      </div>
      <Settings height={height} />
      {modal === "weapons" && (
        <WpInventory
          wpType={charData.weapon}
          close={closeInv}
          btnText="Pick"
          btnClick={({ user, ...wpInfo }) => dispatch(PICK_WP_IN_DB(wpInfo))}
        />
      )}
      {prePickerOn && (
        <PrePicker
          choices={artifactIcons}
          pick={(artType) => {
            setModal(artType);
            setPrePickerOn(false);
          }}
          close={() => setPrePickerOn(false)}
          pickSetOption={
            <ButtonBar
              className="mt-6"
              texts={["Pick Equipped Set"]}
              variants={["positive"]}
              handlers={[
                () => {
                  setModal("characters");
                  setPrePickerOn(false);
                }
              ]}
            />
          }
        />
      )}
      {![null, "weapons", "characters"].includes(modal) && (
        <ArtPicker artType={modal} close={closeInv} />
      )}
      {modal === "characters" && <CharPicker close={closeInv} />}
    </Card>
  );
}
