import { getFinalTlLv, round2, round3 } from "../../../helpers";
import tlLvMults from "../tlLvMults";

function getEBBnValue(char, partyData) {
  const pyroCount = partyData.reduce(
    (result, data) => {
      return data.vision === "Pyro" ? result + 1 : result;
    },
    checkCons[1](char) ? 1 : 0
  );
  const baseMult = pyroCount === 1 ? 14.88 : pyroCount >= 2 ? 22.32 : 0;
  const level = getFinalTlLv(char, Nahida.actvTalents[2], partyData);
  return round2(baseMult * tlLvMults[2][level]);
}

